.statistics {
  display: flex;
  align-items: center;

  flex-direction: column;
  text-align: center;

  margin: 0 auto;
  padding: 0;
  margin-bottom: 50px;
  width: 370px;
  /* height: 400px; */
  background: #ffffff;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 0px 0px 4px 4px;
}
.statList {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  margin: 0 auto;
  padding: 0;
  list-style: none;

  width: 100%;
}
.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: wrap;
  justify-content: center;
  border: 1px solid #ccc;

  height: 100px;
  width: 100%;
  background-color: rgb(230, 227, 227);
}
.title {
  font-size: 24px;
  font-weight: 600;
}
.label {
  font-size: 12px;
}
.percentage {
  font-size: 22px;
}
